import React, { Component } from 'react';
import { Row, Col, Button } from 'reactstrap';

import footerBG from 'assets/images/landing/footer-bg.svg';

import styles from './Footer.module.scss';

class Footer extends Component {
  renderEnquire() {
    return (
      <div className={styles.enquireContainer}>
        <h3>Enquire Now</h3>
        <p>
          Speak with us for a complimentary session on how to use video to boost 
          your branding! Our experienced team will guide you towards the video 
          you need.
        </p>
        <Button>
          MESSAGE US!
        </Button>
      </div>
    )
  }

  renderFooterInfo() {
    return (
      <div className={styles.footerInfo}>
        <Row noGutters>
          <Col xs={7}>
            <div className="d-flex">
              <p>HOME</p>
              <p>ABOUT US</p>
              <p>FAQ</p>
              <p>CHANGE LOG</p>
              <p>CONTACT</p>
            </div>
            <div>© 2019 INFINITE FRONTIER</div>
          </Col>
          <Col xs={5}>
            <div className="d-flex">
              <p>PRIVACY POLICY</p>
              <p>TERMS OF SERVICE</p>
            </div>
          </Col>
        </Row>
      </div>
    );
  }

  render() {
    return (
      <div className={styles.footer}>
        <div className={styles.footerBG}>
          <img src={footerBG} alt="footer-bg" />
        </div>
        {this.renderEnquire()}
        {this.renderFooterInfo()}
      </div>
    );
  }
}

export default Footer;
